"use client"

import Link from "next/link"
import { usePathname } from "next/navigation"
import {
  LayoutDashboard,
  FolderOpen,
  Settings,
  FileText,
  LogOut,
  Plus,
  ChevronRight,
  ChevronLeft,
  PanelLeftClose,
  PanelLeft,
} from "lucide-react"
import { cn } from "@/lib/utils"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { Button } from "@/components/ui/button"

const menuItems = [
  { icon: LayoutDashboard, label: "Dashboard", href: "/dashboard" },
  { icon: FolderOpen, label: "Library", href: "/library" },
  { icon: Settings, label: "Settings", href: "/settings" },
]

interface SidebarProps {
  isCollapsed: boolean
  onToggle: () => void
}

export function Sidebar({ isCollapsed, onToggle }: SidebarProps) {
  const pathname = usePathname()

  return (
    <aside
      className={cn(
        "fixed left-0 top-0 h-full bg-slate-900 text-slate-300 flex flex-col z-40 transition-all duration-300 ease-in-out",
        isCollapsed ? "w-20" : "w-64"
      )}
    >
      {/* Logo & Toggle */}
      <div className={cn("flex items-center gap-3 py-6", isCollapsed ? "px-4 justify-center" : "px-5")}>
        <div className="w-9 h-9 bg-gradient-to-br from-blue-500 to-blue-600 rounded-lg flex items-center justify-center shadow-lg shadow-blue-500/25 flex-shrink-0">
          <FileText className="w-5 h-5 text-white" />
        </div>
        {!isCollapsed && (
          <span className="text-lg font-semibold text-white whitespace-nowrap">SOP Builder</span>
        )}
      </div>

      {/* Toggle Button */}
      <div className={cn("px-3 mb-4", isCollapsed && "px-2")}>
        <Button
          variant="ghost"
          size="sm"
          onClick={onToggle}
          className={cn(
            "w-full text-slate-400 hover:text-white hover:bg-slate-800/50 transition-colors",
            isCollapsed ? "justify-center px-2" : "justify-start px-3"
          )}
          title={isCollapsed ? "Expand sidebar" : "Collapse sidebar"}
        >
          {isCollapsed ? (
            <PanelLeft className="w-5 h-5" />
          ) : (
            <>
              <PanelLeftClose className="w-5 h-5 mr-2" />
              <span className="text-sm">Collapse</span>
            </>
          )}
        </Button>
      </div>

      {/* Quick Create Button */}
      <div className={cn("mb-6", isCollapsed ? "px-3" : "px-4")}>
        <Link
          href="/create"
          className={cn(
            "flex items-center gap-3 bg-gradient-to-r from-blue-600 to-blue-500 hover:from-blue-500 hover:to-blue-600 text-white rounded-xl font-medium transition-all shadow-lg shadow-blue-600/25 hover:shadow-blue-500/40",
            isCollapsed ? "w-14 h-14 justify-center p-0" : "w-full px-4 py-3"
          )}
          title="Create New SOP"
        >
          <Plus className="w-5 h-5 flex-shrink-0" />
          {!isCollapsed && <span>New SOP</span>}
        </Link>
      </div>

      {/* Navigation */}
      <nav className="flex-1 px-3">
        {!isCollapsed && (
          <div className="text-xs font-medium text-slate-500 uppercase tracking-wider px-3 mb-3">
            Menu
          </div>
        )}
        <ul className="space-y-1">
          {menuItems.map((item) => {
            const isActive = pathname === item.href || pathname.startsWith(item.href + "/")
            return (
              <li key={item.href}>
                <Link
                  href={item.href}
                  className={cn(
                    "flex items-center gap-3 rounded-xl transition-all group",
                    isCollapsed ? "justify-center p-3" : "px-4 py-2.5",
                    isActive
                      ? "bg-slate-800 text-white shadow-md"
                      : "hover:bg-slate-800/50 hover:text-white"
                  )}
                  title={isCollapsed ? item.label : undefined}
                >
                  <item.icon
                    className={cn(
                      "w-5 h-5 flex-shrink-0 transition-colors",
                      isActive ? "text-blue-400" : "text-slate-400 group-hover:text-slate-300"
                    )}
                  />
                  {!isCollapsed && (
                    <>
                      <span className="flex-1">{item.label}</span>
                      {isActive && <ChevronRight className="w-4 h-4 text-slate-500" />}
                    </>
                  )}
                </Link>
              </li>
            )
          })}
        </ul>
      </nav>

      {/* User Section */}
      <div className="border-t border-slate-800 p-4">
        <div
          className={cn(
            "flex items-center gap-3 rounded-xl hover:bg-slate-800/50 transition-colors cursor-pointer",
            isCollapsed ? "justify-center p-2" : "px-2 py-2"
          )}
          title={isCollapsed ? "John Doe" : undefined}
        >
          <Avatar className="w-9 h-9 bg-gradient-to-br from-blue-500 to-blue-600 flex-shrink-0 shadow-lg shadow-blue-500/25">
            <AvatarFallback className="bg-gradient-to-br from-blue-500 to-blue-600 text-white text-sm font-medium">
              JD
            </AvatarFallback>
          </Avatar>
          {!isCollapsed && (
            <div className="flex-1 min-w-0">
              <p className="text-sm font-medium text-white truncate">John Doe</p>
              <p className="text-xs text-slate-500 truncate">john@company.com</p>
            </div>
          )}
        </div>
        <button
          className={cn(
            "flex items-center gap-3 w-full mt-2 text-sm text-slate-400 hover:text-white hover:bg-slate-800/50 rounded-xl transition-colors",
            isCollapsed ? "justify-center p-3" : "px-4 py-2.5"
          )}
          title={isCollapsed ? "Sign out" : undefined}
        >
          <LogOut className="w-4 h-4 flex-shrink-0" />
          {!isCollapsed && <span>Sign out</span>}
        </button>
      </div>
    </aside>
  )
}

